<template>
    <div class="centerPage">
        <div class="section">
            <div class="leftAlign">
                <div>
                    <h2>
                        Contact Me
                    </h2>
                    <b-form @submit="onSubmit"
                            @reset="onReset"
                            v-if="show">
                        <b-form-group id="input-group-1"
                                      label="Name:"
                                      label-for="input-1">
                            <b-form-input id="input-1"
                                          size="sm"
                                          v-model="form.name"
                                          placeholder="Enter name"></b-form-input>
                        </b-form-group>

                        <b-form-group id="input-group-2"
                                      label="Email:"
                                      label-for="input-2">
                            <b-form-input id="input-2"
                                          size="sm"
                                          v-model="form.email"
                                          type="email"
                                          placeholder="Enter email"></b-form-input>
                        </b-form-group>

                        <b-form-group id="input-group-3"
                                      label="Message:"
                                      label-for="input-3">
                            <b-form-textarea id="input-3"
                                             size="sm"
                                             v-model="form.message"
                                             placeholder="Enter message"
                                             rows="4"
                                             no-resize></b-form-textarea>
                        </b-form-group>
                        <div class="centerAlign">
                            <b-button type="submit"
                                      variant="info"
                                      size="sm"
                                      class="btnSpace">Send</b-button>
                            <b-button type="reset"
                                      variant="danger"
                                      size="sm">Reset</b-button>
                        </div>
                    </b-form>
                </div>
                <div class="centerAlign">
                    <div class="mediaZoom">
                        <b-avatar size="md"
                                  icon="github"
                                  href="https://github.com/j-ayala"
                                  target="_blank"></b-avatar>
                    </div>
                    <div class="mediaZoom">
                        <b-avatar size="md"
                                  icon="linkedin"
                                  href="http://linkedin.com/in/jason-ayala"
                                  target="_blank"></b-avatar>
                    </div>
                    <div class="mediaZoom">
                        <b-avatar size="md"
                                  icon="facebook"
                                  href="https://www.facebook.com/jason.ayalala"
                                  target="_blank"></b-avatar>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Contact',
        data() {
            return {
                form: {
                    name: '',
                    email: '',
                    message: '',
                },
                show: true
            }
        },
        methods: {
            makeToast(message, title, variant) {
                this.$bvToast.toast(message, {
                    title: title,
                    variant: variant,
                    toaster: 'b-toaster-top-center',
                    solid: true
                })
            },
            onSubmit(event) {
                event.preventDefault()
                this.makeToast("Email successfully sent!", "success", "success");
            },
            onReset(event) {
                event.preventDefault()
                // Reset our form values
                this.form.email = ''
                this.form.name = ''
                this.form.message = ''
                // Trick to reset/clear native browser form validation state
                this.show = false
                this.$nextTick(() => {
                    this.show = true
                })
            }
        }
    }
</script>